@import '~src/css/quasar.variables.scss', 'quasar/src/css/variables.sass';











































































































































































.q-item__section--side {
  padding-right: 0;
}
.q-page{
  overflow: hidden;
  @media (max-height: $breakpoint-xs-max) {
    overflow:inherit;
  }
}
.mobile,
.desktop {
  height: 70vh;
  margin: 0 0.3rem;
}
.logo {
  margin: auto;
  height: 30vh;

  @media (max-width: $breakpoint-md-max) {
    height: 30vh;
  }

  @media (max-width: $breakpoint-xs-max) {
      height: 25vh;
  }
}
.container {
  margin-top: -2rem;
}
.platforms {
  top: 38vh;

  @media (max-height: $breakpoint-xs-max) and (orientation : landscape){
    display: none;
  }
}
.q-footer {
  margin: 10px auto 0;
  background-color: white;
  color: $primary;

  @media (max-height: $breakpoint-xs-max) and (orientation : landscape){
    position: absolute;
    bottom: auto;
    top: 70vh;
  }
}
.text {
  cursor: pointer;
  color: $primary;
}
